//==========Close container===========
.close-container--sign {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 0;
  right: 12px;
  margin: 0 auto;
  width: 25px;
  height: 25px;
  cursor: pointer;

  @include tablet {
    top: 12px;
    right: 24px;
  }
}

.leftright--sign,
.rightleft--sign {
  width: 20px;
  height: 4px;
  width: 20px;
  position: absolute;
  margin-top: 24px;
  background-color: $color-dark;
  border-radius: 2px;
  transform: rotate(45deg);
  transition: all 0.3s ease-in;

  @include tablet {
    width: 25px;
  }
}

.rightleft--sign {
  transform: rotate(-45deg);
}

.close-container--sign:hover .leftright--sign {
  transform: rotate(-45deg);
  background-color: $color-blue;

  transition: all 0.5s;
  box-shadow: 0 8px 43px $color-blue;
}

.close-container--sign:hover .rightleft--sign {
  transform: rotate(45deg);
  background-color: $color-blue;

  transition: all 0.5s;
  box-shadow: 0 8px 43px $color-blue;
}

