.backdrop-form {
  display: none;
  position: fixed;
  z-index: 12;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow-y: hidden;
  background: $color-blue;
}

.ellipse {
  position: absolute;
  width: 1421px;
  height: 1421px;
  left: -846px;
  top: -918px;
  background: rgba(204, 236, 231, 0.5);
  filter: blur(539.791px);
}

.modal-content {
  padding: 40px 20px 40px;
  width: 90%;
  font-family: 'DM Sans';
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 24px;

  letter-spacing: -0.01em;
  text-transform: uppercase;

  color: #f3f3f3;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  background: #f6f6f6;

  border-radius: 18px;
  border: 2px solid $color-dark;

  @include tablet {
    width: 579px;
    // height: 516px;

    padding: 80px 40px 40px;
  }
}

.links-container {
  display: flex;
  justify-content: center;
  margin-top: 20px;
}

.links-container a {
  font-size: 14px;
  line-height: 1.29;

  letter-spacing: -0.01em;
  color: rgba(17, 17, 17, 0.5);

  margin: 0 10px;
  border-bottom: 2px solid transparent;
  @include transition(color);
  @include transition(border-bottom);
  @include tablet {
    font-size: 18px;
    line-height: 1.33;
    letter-spacing: -0.01em;
  }
}

.links-container a.active,
.links-container a:hover {
  border-bottom: 2px solid $color-blue;
  color: $color-blue;
}

.visible {
  display: block;
}

.hidden {
  display: none;
}

.registration-form,
.login-form {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 24px;
  @include tablet {
    gap: 28px;
  }
}

.registration-form__input,
.login-form__input {
  width: 100%;
  height: 50px;
  font-family: 'DM Sans';
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 1.12px;
  letter-spacing: -0.01em;
  color: #ffffff;
  padding-left: 40px;

  color: $color-dark;
  text-transform: uppercase;
  border: 2px solid $color-dark;
  border-radius: 40px;

  @include tablet {
    font-size: 18px;
    line-height: 1.33px;
    width: 499px;
    height: 64px;
  }
}

.registration-form__btn,
.login-form__btn {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px 0;
  gap: 10px;

  font-family: 'DM Sans';
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 1.12;
  /* identical to box height, or 112% */

  letter-spacing: -0.01em;
  text-transform: uppercase;

  width: 100%;
  color: #f3f3f3;
  background-color: #111111;
  border-radius: 40px;
}
.registration-form__input {
  outline: none;
}

.registration-form__input:hover,
.registration-form__input:focus,
.login-form__input:hover,
.login-form__input:focus {
  border-color: $color-blue;
  transition: all 0.5s;
}
.registration-form__btn:hover,
.login-form__btn:hover,
.registration-form__btn:focus,
.login-form__btn:focus {
  background-color: $color-blue;
  color: $color-white;
  transition: all 0.5s;
}

::placeholder {
  color: $color-dark;
}
