.footer {
  padding: 29px 0;
  position: sticky;
  box-shadow: 0 2px 8px #5f41af80, inset 0 1px #ffffff4d, inset 0 10px #fff3, inset 0 10px 20px #ffffff40, inset 0 -15px 30px #7e35d34d;
}


.footer__wrapper {

  font-family: 'Roboto';
  font-size: 14px;
  line-height: 1.14;
  text-align: center;

  color:  #6f4bca;

  @media screen and (min-width: 768px) {
    font-size: 16px;
    line-height: 1.19;
  }
}

.footer__text {
  @media screen and (min-width: 768px) {
    display: inline-block;
  }
}

.footer__text--left {
  @media screen and (max-width: 768px) {
    margin-bottom: 10px;
  }
}

.footer__text--rigth {
  display: inline-flex;
  align-items: center;
  justify-content: center;
}

.footer__icon {
  margin-left: 10px;
  margin-right: 10px;
}

.footer__link {
  color: currentColor;
  margin-left: 4px;
}

.students--link { 
  background: -webkit-linear-gradient(left, #9584db, #8a78d1, #5a4b97,  #5f43ce, #9584db);
  background: linear-gradient(left, #9584db, #8a78d1, #5a4b97,  #5f43ce, #9584db);
  background-size: 3200px 200px;
  color: transparent;
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-animation: animate-logo;
  animation: animate-logo;
  -webkit-animation-delay: 0;
  animation-delay: 0;
  -webkit-animation-duration: 10s;
  animation-duration: 10s;
  -webkit-animation-timing-function: linear;
  animation-timing-function: linear;
  -webkit-animation-iteration-count: infinite;
  animation-iteration-count: infinite;
  -webkit-animation-fill-mode: forwards;
  animation-fill-mode: forwards;
  transition: font-size 500ms ease-in-out;
  }
  
  @keyframes animate-logo {
  0% {
  background-position: 0 3200px;
  }
  100% {
  background-position: 3200px 0;
  }
  }

