html, body {
  height: 100%;
  margin: 0;
  padding: 0;
}
.wrapper--body {
 display: flex;
 flex-direction: column;
 min-height: 100%;
}

.main--wrapper {
  flex: 1;
}
.footer {
  margin-top: auto;
}

  

